

import HeaderBar from '../../../components/header.vue'
import {
    mapState, mapActions
}
from 'vuex'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
    components: {
        HeaderBar,
        XInput,
        Group,
        Cell,
        Tabbar,
        TabbarItem,
        Datetime,
        XTextarea,
        XSwitch,
        Actionsheet
    },
    computed: {
        ...mapState([
            'rooms',
            'roomid'
        ])
    },
    mounted() {
        this.checkRoomID()
    },
    methods: {
        change(value) {
                console.log('change', value)
            },
            checkRoomID() {
                const roomid = this.roomid
                let room_detail
                if (this.roomid) {
                    room_detail = this.rooms.filter(function(currentValue, index, arr) {
                        return currentValue.id == roomid
                    })
                    if (room_detail) {
                        this.username = room_detail[0].tenant
                        this.start_date = room_detail[0].start_date
                        this.end_date = room_detail[0].expiry_date
                        this.monthly_date = room_detail[0].monthly_date
                        this.deposit = room_detail[0].deposit
                        this.monthly_rent = room_detail[0].monthly_rent
                        this.first_rent = room_detail[0].first_rent
                        this.state = room_detail[0].isRent
                        this.notes = room_detail[0].notes
                        this.isAddButton = false
                    }
                }
            },
            addButton() {
                this.show1 = true;
            },
            modifyButton() {
                this.show = true;
            },
            click(key) {
                console.log(key)
            }
    },
    data() {
        return {
            format: 'MM-DD',
            start_date: '2015/11/12',
            end_date: '2017/05/01',
            monthly_date: '28',
            Property: '透天',
            deposit: 0,
            monthly_rent: '',
            first_rent: '',
            username: '',
            isAddButton: true,
            state: false,
            notes: '',
            show: false,
            menus: {
                'title.noop': '你確定嗎?',
                modify: '<span style="color:red">修改</span>'
            },
            show1: false,
            menus1: {
                'title.noop': '你確定嗎?',
                add: '<span style="color:red">新增</span>'
            }
        }
    }
}




