

import HeaderBar from '../../../components/header.vue'
import {
    mapState, mapActions
}
from 'vuex'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
    components: {
        HeaderBar,
        XInput,
        Group,
        Cell,
        Checker,
        CheckerItem,
        Tabbar,
        TabbarItem,
        Actionsheet
    },
    computed: {
        ...mapState([
            'houses',
            'houseid'
        ])
    },
    mounted() {
        this.checkHouseID()
    },
    methods: {
        checkHouseID() {
                const houseid = this.houseid
                let houseid_detail
                if (this.houseid) {
                    houseid_detail = this.houses.filter(function(currentValue, index, arr) {
                        return currentValue.id == houseid
                    })
                    if (houseid_detail) {
                        this.address = houseid_detail[0].address
                        this.house_name = houseid_detail[0].name
                        this.isAddButton = false
                    }
                }
            },
            addButton() {
                this.show1 = true;
            },
            modifyButton() {
                this.show = true;
            },
            click(key) {
                console.log(key)
            }
    },
    data() {
        return {
            Property: '透天',
            code: '',
            account: '',
            jointly: '',
            address: '',
            house_name: '',
            isAddButton: true,
            show: false,
            menus: {
                'title.noop': '你確定嗎?',
                modify: '<span style="color:red">修改</span>'
            },
            show1: false,
            menus1: {
                'title.noop': '你確定嗎?',
                add: '<span style="color:red">新增</span>'
            }
        }
    }
}




